import React, { Component } from "react";
import * as THREE from "three";
const OrbitControls = require('three-orbitcontrols')

// import * as OrbitControls from "three";
// const OrbitControls = require('three')


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      rotation: 0.005,
      material: new THREE.MeshStandardMaterial(  ),
      geometry: new THREE.BoxGeometry( 2, 2, 2 ),
      color: 0xFFFFFF,
      side: THREE.DoubleSide,
      wireframe: false
    };
  }

  SetGeometry(geometry) {
    this.setState({
        geometry: geometry
      });    
  }


  SetRotation(float) {
    this.setState({
      rotation: float
    });
  }


  SetMaterial(material) {
    this.setState({
      material: material
    });
  }

  SetColor(color) {
    this.setState({
      color: color
    });
  }


  SetSide(Side) {
    this.setState({
      side: Side
    });
  }


  ToggleWireFrame() {
    this.setState({
      wireframe: !this.state.wireframe
    });
  }


  componentDidUpdate(prevprops) {
    if (prevprops.needsupdate !== this.props.needsupdate) { this.StopRotation()}
  }



  componentDidMount() {
    // === THREE.JS CODE START ===


   



    function scene() {    
      scene = new THREE.Scene();
      scene.background = new THREE.Color( 0x121213 );  
      
      return scene
    }




    function SetCamera(x,y,z) {


      var camera = new THREE.PerspectiveCamera( 
        45, 
        window.innerWidth/window.innerHeight, 
        0.1, 
        1000 );

       camera.position.set( x,y,z);

        return camera
    }




  

    function renderIt () {
      var renderer = new THREE.WebGLRenderer({ antialias: true });

      renderer.setSize( window.innerWidth, window.innerHeight );

      
      renderer.setPixelRatio( window.devicePixelRatio );
      
      renderer.gammaFactor = 2.2;
      renderer.gammaOutput = true;

      renderer.physicallyCorrectLights = true;

      document.body.appendChild( renderer.domElement );

      return renderer
    }


    function onWindowResize() {
      // set the aspect ratio to match the new browser window aspect ratio
      this.camera.aspect = window.innerWidth / window.innerHeight;
    
      // update the camera's frustum
      this.camera.updateProjectionMatrix();
    
      // update the size of the renderer AND the canvas
      this.renderer.setSize( window.innerWidth, window.innerHeight );
      }
      window.addEventListener( 'resize', onWindowResize );



    function returnAmbientLights() {
    
      const ambientLight = new THREE.HemisphereLight(
          0xddeeff, // bright sky color
          0x202020, // dim ground color
          3, // intensity
        );
    
      return ambientLight
  
    }

    

    function returnMainLights() {
      
        const mainLight = new THREE.DirectionalLight( 0xffffff, 5 );
        mainLight.position.set( 10, 10, 10 );
      
        return mainLight
    
      }


      function returnPlane() {
        var geometry = new THREE.PlaneGeometry( 10, 10, 1 );
        var material = new THREE.MeshStandardMaterial( 
            {color: 0x1E90FF, 
                side: THREE.DoubleSide, 
                transparent: true, 
                opacity: 0.6, 
                wireframe: false} 
            );
        var plane = new THREE.Mesh( geometry, material );
        
        return plane
      }


    

    this.scene = scene();
    this.camera = SetCamera(0,-15,5)
    this.scene.add( returnMainLights(), returnAmbientLights() );



    this.renderer = renderIt ()
    var controls = new THREE.OrbitControls( this.camera, this.renderer.domElement );



    this.cube = new THREE.Mesh( this.state.geometry, this.state.material )
    this.scene.add( this.cube );

    var plane = returnPlane()
    this.scene.add(plane)
    plane.position.set(0,0,-3)



    this.start()

    // === THREE.JS EXAMPLE CODE END ===
  }





animate = () => {

    this.cube.geometry = this.state.geometry

    this.cube.material = this.state.material
    this.cube.material.color.setHex(this.state.color)
    this.cube.material.side = this.state.side
    this.cube.material.wireframe = this.state.wireframe


      this.cube.rotation.x += this.state.rotation
      this.cube.rotation.y += this.state.rotation

      this.renderScene()
      this.frameId = window.requestAnimationFrame(this.animate)
  }


start = () => {
            if (!this.frameId) {
                this.frameId = requestAnimationFrame(this.animate)
            }
  }


stop = () => {
    cancelAnimationFrame(this.frameId)
  }


renderScene = () => {
    this.renderer.render(this.scene, this.camera)
  }


  render() {
    return (
      <>


      <div style={{position: "absolute", color: "white", fontSize: "20px", padding: "50px"}}>

        <p>three.js react app using states</p>

      <button onClick={ () => this.SetGeometry(new THREE.BoxGeometry( 2, 2, 2 ) ) } >Cube</button>
      <button onClick={ () => this.SetGeometry( new THREE.SphereGeometry( 2 ) ) } >Sphere</button>

      <br /><br />


            <button onClick={ () => this.SetRotation(0) } >Stop Rotation</button>
            <button onClick={ () => this.SetRotation(0.005) } >Start Rotation</button>

            <br />
            <button onClick={ () => this.SetColor(0x1E90FF) } >Blue</button>
            <button onClick={ () => this.SetColor(0xFFFFFF) } >White</button>

            <br />


            <br />
            <button onClick={ () => this.ToggleWireFrame() } >toggle wireframe</button>

            <br /><br />

            <button onClick={ () => this.SetMaterial(new THREE.MeshStandardMaterial( {color: this.state.color} ) ) }  >Standard</button>

            <button onClick={ () => this.SetMaterial(new THREE.MeshStandardMaterial( {
    metalness: 1,   // between 0 and 1
    roughness: 0.5, // between 0 and 1
    // envMap: envMap,
} )   ) }  >Metal</button>


            

            <br /><br />
            <button onClick={ () => this.SetMaterial(new THREE.MeshLambertMaterial( {color: this.state.color, transparent: true, opacity: 0.5, depthTest: false, depthWrite: false, side:this.state.side})) } >Transparent</button>

            <br />

            <button onClick={ () => this.SetSide(THREE.DoubleSide) } >Side: Double</button>
            <button onClick={ () => this.SetSide(THREE.FrontSide) } >Front</button>
            <button onClick={ () => this.SetSide(THREE.BackSide) } >Back</button>


            <br /><br />
            <button onClick={ () => this.SetMaterial(new THREE.MeshPhysicalMaterial( 
                {color: this.state.color, 
                    flatShading: true,
                    reflectivity: 0.8,
                    metalness: 0.8,
                    roughness: 0.4,
                // transparent: true, 
                // opacity: 0.5, 
                // depthTest: false, 
                // depthWrite: false, 
                side:this.state.side})) } >Phyiscal</button>

            

      </div>


      </>

    )
  }
}


export default App